@charset "utf-8";

.about {
  &__flex {
    display: flex;
    justify-content: space-around;
    @media #{$mq_tab}{
      display: block;
    }
  }

  &__img {
    width: 40rem;
    height: 40rem;
    border-radius: 50%;
    @media #{$mq_tab}{
      width: 80rem;
      max-width: 80rem;
      height: auto;
      margin-bottom: 6rem;
    }
  }

  .table {
    font-size: 2rem;
    text-align: left;
    @media #{$mq_tab}{
      font-size: 3rem;
      line-height: 5rem;
      width: 100%;
    }

    .table__item {
      display: block;
      padding: 3rem 0;
      border-bottom: 1px dotted #000;
      @media #{$mq_tab}{
        width: 100%;
      }

      &:first-child {
        padding-top: 0;
      }
    }

    &__title {
      padding-right: 2rem;
    }

    a {
      color: #0000ff;
    }
  }
}