@charset "UTF-8";

/* ------------------------------

  vw / vh 計算

------------------------------ */

//vw計算（スマホ）
@function calc_vw($base_size, $viewport:750){
  @return $base_size / $viewport * 100vw;
}

//vw計算（デスクトップ）
@function calc_vw_desktop($base_size, $viewport:1980){
  @return $base_size / $viewport * 100vw;
}

//vh計算 - Macbook(13inch - 1440*720)
@function calc_vh_desktop($base_size, $viewport:720){
  @return $base_size / $viewport * 100vh;
}