@charset "utf-8";

.service {
  background: #4da6ff;

  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 5rem;
    @media #{$mq_tab}{
      display: block;
    }
  }

  &__item {
    width: 30%;
    font-weight: bold;
    opacity: 0;
    @media #{$mq_tab}{
      width: 100%;
      margin-bottom: 13rem;
    }

    &.active {
      opacity: 1;
      transform: translate(0, 0);
    }
  
    &:nth-child(3n-1){
      &.active {
        transition-delay: .2s;
        @media #{$mq_sp} {
          transition-delay: 0s;
        }
      }	
    }
    &:nth-child(3n){
      &.active {
        transition-delay: .4s;
        @media #{$mq_sp} {
          transition-delay: 0s;
        }
      }	
    }
    @media #{$mq_sp} {
      &:nth-child(2n){
        &.active {
          transition-delay: .2s;
        }
      }
    }
  }

  &__img {
    height: 20rem;
    margin-bottom: 3rem;
    @media #{$mq_tab}{
      height: 30rem;
      margin-bottom: 5rem;
    }
  }

  &__title {
    margin-bottom: 2rem;
    font-size: 3rem;
    @media #{$mq_tab}{
      margin-bottom: 4rem;
      font-size: 5rem;
    }
  }

  &__text {
    line-height: 3rem;
    .underline-yellow {
      // border-bottom: 5px solid #ffff80;
      background: linear-gradient(transparent 60%, #ffff80 10%);
    }
    @media #{$mq_tab}{
      font-size: 4rem;
      line-height: 7rem;
      font-weight: 500;
      text-align: left;
    }
  }
}