@charset "UTF-8";

.footer {
  background: #000;
  color: #4da6ff;
  height: 5rem;
  text-align: center;
  position: relative;
  @media #{$mq_tab}{
    height: 10rem;
  }

  .copyright {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 1rem;
    @media #{$mq_tab}{
      font-size: 3rem;
    }
  }
}