.about__flex { display: flex; justify-content: space-around; }

@media screen and (max-width: 1024px) { .about__flex { display: block; } }

.about__img { width: 40rem; height: 40rem; border-radius: 50%; }

@media screen and (max-width: 1024px) { .about__img { width: 80rem; max-width: 80rem; height: auto; margin-bottom: 6rem; } }

.about .table { font-size: 2rem; text-align: left; }

@media screen and (max-width: 1024px) { .about .table { font-size: 3rem; line-height: 5rem; width: 100%; } }

.about .table .table__item { display: block; padding: 3rem 0; border-bottom: 1px dotted #000; }

@media screen and (max-width: 1024px) { .about .table .table__item { width: 100%; } }

.about .table .table__item:first-child { padding-top: 0; }

.about .table__title { padding-right: 2rem; }

.about .table a { color: #0000ff; }

.contact { background: #4da6ff; }

.contact__form { width: 60rem; margin: 0 auto; }

@media screen and (max-width: 1024px) { .contact__form { width: 100%; } }

.contact label, .contact input, .contact textarea { display: block; }

.contact input, .contact textarea { width: 100%; margin-bottom: 4rem; border-radius: 1rem; border: none; padding: 1rem; }

@media screen and (max-width: 1024px) { .contact input, .contact textarea { margin-bottom: 6rem; } }

.contact__label { text-align: left; font-size: 2rem; font-weight: bold; margin-bottom: 2rem; }

@media screen and (max-width: 1024px) { .contact__label { font-size: 3rem; } }

.contact__label .must { margin-left: 2rem; background: #ffff80; border-radius: 6rem; padding: .1rem 1rem; }

.contact input { height: 5rem; }

@media screen and (max-width: 1024px) { .contact input { height: 7rem; } }

.contact textarea { height: 30rem; }

@media screen and (max-width: 1024px) { .contact textarea { height: 50rem; } }

.contact__btn { background: #ffff80; font-size: 2rem; font-weight: bold; padding: 2rem 8rem; border-radius: 3rem; }

@media screen and (max-width: 1024px) { .contact__btn { font-size: 5rem; padding: 0; width: 100%; height: 15rem; border-radius: 8rem; } }

.contact__btn:hover { opacity: .8; }

.message__list-1column { margin: 0 auto; padding: 0 0 9rem; border-bottom: 1px dotted #000; }

.message__title { font-size: 3rem; margin-bottom: 3rem; }

@media screen and (max-width: 1024px) { .message__title { font-size: 6rem; margin-bottom: 5rem; } }

.message__text { line-height: 3rem; font-weight: bold; color: #333; }

@media screen and (max-width: 1024px) { .message__text { font-size: 3rem; line-height: 6rem; text-align: left; font-weight: 500; } }

.message__list-2column { display: flex; justify-content: space-around; width: 100%; margin-top: 9rem; }

@media screen and (max-width: 1024px) { .message__list-2column { display: block; } }

.message__list-2column .message__item { width: 45%; }

@media screen and (max-width: 1024px) { .message__list-2column .message__item { width: 100%; margin-bottom: 9rem; } }

.section { padding: 10rem 0; text-align: center; }

.section__title { font-size: 4rem; display: inline-block; background: linear-gradient(transparent 60%, #ffff80 60%); margin-bottom: 5rem; }

@media screen and (max-width: 1024px) { .section__title { font-size: 6rem; margin-bottom: 8rem; } }

.section__detail { background: #fff; display: inline-block; padding: 3rem; border-radius: 5rem; }

@media screen and (max-width: 1024px) { .section__detail { font-size: 3rem; line-height: 5rem; border-radius: 10rem; } }

.section__detail a { color: #0000ff; border-bottom: 1px solid #0000ff; opacity: .8; font-weight: bold; }

.section__detail a:hover { opacity: 1; }

.service { background: #4da6ff; }

.service__list { display: flex; justify-content: space-between; flex-wrap: wrap; margin-bottom: 5rem; }

@media screen and (max-width: 1024px) { .service__list { display: block; } }

.service__item { width: 30%; font-weight: bold; opacity: 0; }

@media screen and (max-width: 1024px) { .service__item { width: 100%; margin-bottom: 13rem; } }

.service__item.active { opacity: 1; transform: translate(0, 0); }

.service__item:nth-child(3n-1).active { transition-delay: .2s; }

@media screen and (max-width: 1024px) { .service__item:nth-child(3n-1).active { transition-delay: 0s; } }

.service__item:nth-child(3n).active { transition-delay: .4s; }

@media screen and (max-width: 1024px) { .service__item:nth-child(3n).active { transition-delay: 0s; } }

@media screen and (max-width: 1024px) { .service__item:nth-child(2n).active { transition-delay: .2s; } }

.service__img { height: 20rem; margin-bottom: 3rem; }

@media screen and (max-width: 1024px) { .service__img { height: 30rem; margin-bottom: 5rem; } }

.service__title { margin-bottom: 2rem; font-size: 3rem; }

@media screen and (max-width: 1024px) { .service__title { margin-bottom: 4rem; font-size: 5rem; } }

.service__text { line-height: 3rem; }

.service__text .underline-yellow { background: linear-gradient(transparent 60%, #ffff80 10%); }

@media screen and (max-width: 1024px) { .service__text { font-size: 4rem; line-height: 7rem; font-weight: 500; text-align: left; } }

.skill__title { font-size: 3rem; line-height: 4rem; margin-bottom: 4rem; }

@media screen and (max-width: 1024px) { .skill__title { font-size: 6rem; line-height: 10rem; font-weight: 900; } }

.skill__text { width: 70%; margin: 0 auto; text-align: left; line-height: 4rem; }

@media screen and (max-width: 1024px) { .skill__text { width: 100%; font-size: 3rem; line-height: 6rem; font-weight: 500; } }

.works { background: #4da6ff; }

.works__link { display: block; background: #000; color: #4da6ff; font-weight: bold; padding: 3rem 4rem; width: 20rem; border-radius: 5rem; margin: 0 auto 5rem; }

@media screen and (max-width: 1024px) { .works__link { width: 80%; padding: 5rem; border-radius: 7rem; font-size: 4rem; margin-bottom: 7rem; } }

.single { min-width: 30rem; margin: 0 auto 50px; }

@media screen and (max-width: 1024px) { .single { max-width: 100%; margin: 0 auto 12rem; } }

.single img { width: 100%; }

.container { margin: 0 auto; max-width: 70%; padding: 0 1rem; }

@media screen and (max-width: 1024px) { .container { min-width: 90%; } }

.footer { background: #000; color: #4da6ff; height: 5rem; text-align: center; position: relative; }

@media screen and (max-width: 1024px) { .footer { height: 10rem; } }

.footer .copyright { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); font-size: 1rem; }

@media screen and (max-width: 1024px) { .footer .copyright { font-size: 3rem; } }

.hero { position: relative; background: #4da6ff; height: 100vh; }

@media screen and (max-width: 1024px) { .hero { height: 100vh; } }

.hero-container { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); margin: 0 auto; text-align: center; width: 80%; }

.hero__subtitle { font-size: 5rem; }

@media screen and (max-width: 1024px) { .hero__subtitle { font-size: 8rem; } }

.hero__title { margin: 7rem 0; font-size: 10rem; }

@media screen and (max-width: 1024px) { .hero__title { font-size: 15rem; } }

.header { top: -100px; position: absolute; width: 100%; height: 10rem; margin: 100px auto 0; line-height: 1; z-index: 999; color: #fff; text-decoration: none; }

@media screen and (max-width: 1024px) { .header { top: 0; position: fixed; margin-top: 0; height: auto; } }

.header a { color: #000; text-decoration: none; }

.header__inner { width: 90%; height: 100%; margin: 0 auto; position: relative; }

@media screen and (max-width: 1024px) { .header__inner { width: 100%; height: auto; } }

.header__inner:after { content: ""; clear: both; display: block; }

.mobile-head { background: #fff; width: 100%; height: 56px; z-index: 999; position: relative; }

.g-nav { height: 100%; }

@media screen and (max-width: 1024px) { .g-nav { position: absolute; /* 開いてないときは画面外に配置 */ top: -550px; background: #000; width: 100%; height: auto; text-align: center; -webkit-transition: .5s ease-in-out; -moz-transition: .5s ease-in-out; transition: .5s ease-in-out; } }

.g-nav__list { list-style: none; font-size: 2rem; display: flex; justify-content: flex-end; align-items: center; height: 100%; }

@media screen and (max-width: 1024px) { .g-nav__list { list-style: none; font-size: 14px; display: block; text-align: left; height: auto; margin-left: 10rem; } }

.g-nav__item { padding: 0 2rem; }

@media screen and (max-width: 1024px) { .g-nav__item { margin-bottom: 8rem; }
  .g-nav__item:first-child { margin-top: 5rem; } }

.g-nav__item:last-child { padding-right: 0; }

.g-nav__item a { position: relative; display: inline-block; text-decoration: none; color: #000; }

@media screen and (max-width: 1024px) { .g-nav__item a { color: #fff; font-size: 7rem; } }

.g-nav__item a:after { position: absolute; bottom: -4px; left: 0; content: ''; width: 100%; height: 3px; background: #ffff80; transform: scale(0, 1); transform-origin: center top; transition: transform .3s; }

.g-nav__item a:hover::after { transform: scale(1, 1); }

/* Fixed */
.header.fixed { margin-top: 0; top: 0; position: fixed; height: 8rem; background: #000; transition: top 0.65s ease-in; -webkit-transition: top 0.65s ease-in; -moz-transition: top 0.65s ease-in; max-width: 100%; }

@media screen and (max-width: 1024px) { .header.fixed { background: none; } }

.header.fixed span { background: #000; }

.header.fixed .g-nav a { color: #fff; }

/* Toggle Button */
.nav-toggle { display: none; position: absolute; right: 12px; top: 14px; width: 34px; height: 36px; cursor: pointer; z-index: 101; }

@media screen and (max-width: 1024px) { .nav-toggle { display: block; } }

.nav-toggle div { position: relative; }

.nav-toggle span { display: block; position: absolute; height: 4px; width: 100%; background: #000; left: 0; -webkit-transition: .35s ease-in-out; -moz-transition: .35s ease-in-out; transition: .35s ease-in-out; }

.nav-toggle span:nth-child(1) { top: 0; }

@media screen and (max-width: 1024px) { .nav-toggle span:nth-child(1) { top: 11px; } }

.nav-toggle span:nth-child(3) { top: 22px; }

/* #nav-toggle 切り替えアニメーション */
@media screen and (max-width: 1024px) { .open .nav-toggle span:nth-child(1) { top: 11px; -webkit-transform: rotate(315deg); -moz-transform: rotate(315deg); transform: rotate(315deg); background: #fff; } }

@media screen and (max-width: 1024px) { .open .nav-toggle span:nth-child(2) { width: 0; left: 50%; background: #fff; } }

@media screen and (max-width: 1024px) { .open .nav-toggle span:nth-child(3) { top: 11px; -webkit-transform: rotate(-315deg); -moz-transform: rotate(-315deg); transform: rotate(-315deg); background: #fff; } }

/* #global-nav スライドアニメーション */
@media screen and (max-width: 1024px) { .open .g-nav { /* #global-nav top + #mobile-head height */ -moz-transform: translateY(550px); -webkit-transform: translateY(550px); transform: translateY(550px); } }

