@charset "UTF-8";
@import '../setting/_variable.scss';
@import '../setting/_function.scss';
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600,700,800&display=swap');
@import url('https://fonts.googleapis.com/css?family=Noto+Sans+JP:100,200,300,400,500,600,700,800,900&display=swap&subset=japanese');
@import url('https://fonts.googleapis.com/css?family=Oleo+Script:700&display=swap');


/* ------------------------------

  全体

------------------------------ */

html{
  font-size: 62.5%;
  background-color: #252525;
  @media #{$mq_sp}{
    font-size: 31.25%;
  }
}

body{
  font-family: 'Noto Sans JP','ヒラギノ角ゴ ProN', 'Hiragino Kaku Gothic ProN', 'メイリオ', Meiryo, sans-serif;
  -webkit-font-smoothing: antialiased;
  font-size: 1.6rem;
  font-weight: normal;
  line-height: 1;
  color: $color-txt;
  min-width: 1040px;
  max-width: 1920px;
  margin:auto;
  @media #{$mq_tab}{
    min-width: 1px;
  }
}

/* ------------------------------

  フォント

------------------------------ */
.f-open{
  font-family: 'Open Sans', sans-serif;

  &.-regular{
    font-weight: 400;
  }
  &.-bold{
    font-weight: 700;
  }
}

.f-o{
  font-family: 'Oleo Script', cursive;

  &.-regular{
    font-weight: 400;
  }
  &.-bold{
    font-weight: 700;
  }
}

.f-upper{
  text-transform: uppercase!important;
}

/* ------------------------------

  表示 / 非表示

------------------------------ */

//タブレット以下で非表示
.hidden-tab{
  display: block;
  
  @media #{$mq_tab}{
    display: none;
  }
}

//タブレット以下で表示
.visible-tab{
  display: none;

  @media #{$mq_tab}{
    display: block;
  }
}

//pc以下で非表示
.hidden-pc{
  display: block;
  
  @media #{$mq_pc}{
    display: none;
  }
}

//pc以下で表示
.visible-pc{
  display: none;

  @media #{$mq_pc}{
    display: block;
  }
}

.hidden-sp{
  display: block;

  @media #{$mq_sp}{
    display: none;
  }
}

.visible-sp{
  display: none;

  @media #{$mq_sp}{
    display: block;
  }
}

/* ------------------------------

  リンクの無効

------------------------------ */

.c-click-invalid{
  pointer-events: none;
  a{
    pointer-events: none;  
  }
}

/* ------------------------------

  アクセシビリティ対応

    アイコンには aria-hidden="true"

------------------------------ */

.c-hidden-text{ 
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding:0 !important;
  border:0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

