@charset "utf-8";

.skill {

  &__title {
    font-size: 3rem;
    line-height: 4rem;
    margin-bottom: 4rem;
    @media #{$mq_tab}{
      font-size: 6rem;
      line-height: 10rem;
      font-weight: 900;
    }
  }

  &__text {
    width: 70%;
    margin: 0 auto;
    text-align: left;
    line-height: 4rem;
    @media #{$mq_tab}{
      width: 100%;
      font-size: 3rem;
      line-height: 6rem;
      font-weight: 500;
    }
  }
}