@charset "UTF-8";

/* ------------------------------

  color

------------------------------ */

  $color-txt: #111;
  $color-blue: #2c75e4;
  $color-blue02: #0046ae;
  $color-navy01: #2d3f79;
  $color-navy02: #00388c;
  $color-gray: #efefef;
  $color-gray02: #ececec;
  
/* ------------------------------

  easing

------------------------------ */

  $easing01 : cubic-bezier(0.22, 0.61, 0.36, 1);

/* ------------------------------

  container / breakpoint / mediaQuery

------------------------------ */

  $container_size : 900px;
  $container_size_min : 630px;
  $max_container_size : 1920px;

  //ブレイクポイント
  $pc: $container_size; // デスクトップ
  $tab: 1024px;         // タブレット
  $sp: 1024px;           // スマホ

  //メディアクエリ
  $mq_pc : 'screen and (max-width: #{$max_container_size})';
  $mq_tab : 'screen and (max-width: #{$tab})';
  $mq_sp : 'screen and (max-width: #{$sp})';

  /*
  テンプレート
  @media #{$mq_pc}{}
  @media #{$mq_tab}{}
  @media #{$mq_sp}{}
  */

  .fade_off {
    opacity: 0;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    -ms-transition: all 1s;
    transition: all 1s;
    transform: translate3d(0, 20px, 0);
}

.fade_on {
    opacity: 1;
    transform: translate3d(0, 0, 0);
}

