@charset "UTF-8";

.section {
  padding: 10rem 0;
  text-align: center;

  &__title {
    font-size: 4rem;
    display: inline-block;
    background: linear-gradient(transparent 60%, #ffff80 60%);
    margin-bottom: 5rem;
    @media #{$mq_tab}{
      font-size: 6rem;
      margin-bottom: 8rem;
    }
  }

  &__detail {
    background: #fff;
    display: inline-block;
    padding: 3rem;
    border-radius: 5rem;
    @media #{$mq_tab}{
      font-size: 3rem;
      line-height: 5rem;
      border-radius: 10rem;
    }

    a {
      color: #0000ff;
      border-bottom: 1px solid #0000ff;
      opacity: .8;
      font-weight: bold;
      &:hover {
        opacity: 1;
      }
    }
  }
}