@charset "UTF-8";

.hero {
  position: relative;
  background: #4da6ff;
  height: 100vh;
  @media #{$mq_sp}{
    height: 100vh;
  }

  &-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    margin: 0 auto;
    text-align: center;
    width: 80%;
  }

  &__subtitle {
    font-size: 5rem;
    @media #{$mq_sp}{
      font-size: 8rem;
    }
  }

  &__title {
    margin: 7rem 0;
    font-size: 10rem;
    @media #{$mq_sp}{
      font-size: 15rem;
    }
  }
}

// ヘッダー
.header {
  top: -100px;
  position: absolute;
  width: 100%;
  height: 10rem;
  margin: 100px auto 0;
  line-height: 1;
  z-index: 999;
  color: #fff;
  text-decoration: none;
  @media #{$mq_sp}{
    top: 0;
    position: fixed;
    margin-top: 0;
    height: auto;
  }

  a {
    color: #000;
    text-decoration: none;
  }

  &__inner {
    width: 90%;
    height: 100%;
    margin: 0 auto;
    position: relative;
    @media #{$mq_sp}{
      width: 100%;
      height: auto;
    }
  
    &:after {
      content: "";
      clear: both;
      display: block;
    }
  }
}

.mobile-head {
  background: #fff;
  width: 100%;
  height: 56px;
  z-index: 999;
  position: relative;
}

// グローバルナビ
.g-nav {
  height: 100%;

  @media #{$mq_sp}{
    position: absolute;
    /* 開いてないときは画面外に配置 */
    top: -550px;
    background: #000;
    width: 100%;
    height: auto;
    text-align: center;
    -webkit-transition: .5s ease-in-out;
    -moz-transition: .5s ease-in-out;
    transition: .5s ease-in-out;
  }

  &__list {
    list-style: none;
    font-size: 2rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;

    @media #{$mq_sp}{
      list-style: none;
      font-size: 14px;
      display: block;
      text-align: left;
      height: auto;
      margin-left: 10rem;
    }
  }

  &__item {
    padding: 0 2rem;
    @media #{$mq_sp} {
      margin-bottom: 8rem;
      &:first-child {
        margin-top: 5rem;
      }
    }

    &:last-child {
      padding-right: 0;
    }
    a {
      position: relative;
      display: inline-block;
      text-decoration: none;
      color: #000;
      @media #{$mq_sp} {
        color: #fff;
        font-size: 7rem;
      }
      &:after {
        position: absolute;
        bottom: -4px;
        left: 0;
        content: '';
        width: 100%;
        height: 3px;
        background: #ffff80;
        transform: scale(0, 1);
        transform-origin: center top;
        transition: transform .3s;
      }
      &:hover::after {
        transform: scale(1, 1);
      }
    }
  }
}

/* Fixed */

.header {
  &.fixed {
    margin-top: 0;
    top: 0;
    position: fixed;
    height: 8rem;
    background: #000;
    transition: top 0.65s ease-in;
    -webkit-transition: top 0.65s ease-in;
    -moz-transition: top 0.65s ease-in;
    max-width: 100%;
    @media #{$mq_sp}{
      background: none;
    }

    span {
      background: #000;
    }

    .g-nav {
      a {
        color: #fff;
      }
    }
  }
}

/* Toggle Button */

.nav-toggle {
  display: none;
  position: absolute;
  right: 12px;
  top: 14px;
  width: 34px;
  height: 36px;
  cursor: pointer;
  z-index: 101;
  @media #{$mq_sp}{
    display: block;
  }

  div {
    position: relative;
  }

  span {
    display: block;
    position: absolute;
    height: 4px;
    width: 100%;
    background: #000;
    left: 0;
    -webkit-transition: .35s ease-in-out;
    -moz-transition: .35s ease-in-out;
    transition: .35s ease-in-out;

    &:nth-child(1) {
      top: 0;

      @media #{$mq_sp}{
        top: 11px;
      }     
    }

    // &:nth-child(2) {
      

    // }

    &:nth-child(3) {
      top: 22px;
    }
  }
}



 /* #nav-toggle 切り替えアニメーション */
 .open .nav-toggle span:nth-child(1) {
  @media #{$mq_sp}{
  top: 11px;
  -webkit-transform: rotate(315deg);
  -moz-transform: rotate(315deg);
  transform: rotate(315deg);
  background: #fff;
  }
}
.open .nav-toggle span:nth-child(2) {
  @media #{$mq_sp}{
  width: 0;
  left: 50%;
  background: #fff;
  }
}
.open .nav-toggle span:nth-child(3) {
  @media #{$mq_sp}{
  top: 11px;
  -webkit-transform: rotate(-315deg);
  -moz-transform: rotate(-315deg);
  transform: rotate(-315deg);
  background: #fff;
  }
}
/* #global-nav スライドアニメーション */
.open .g-nav {
  @media #{$mq_sp}{
  /* #global-nav top + #mobile-head height */
  -moz-transform: translateY(550px);
  -webkit-transform: translateY(550px);
  transform: translateY(550px);
  }
}