@charset "utf-8";

.contact {
  background: #4da6ff;

  &__form {
    width: 60rem;
    margin: 0 auto;
    @media #{$mq_tab}{
      width: 100%;
    }
  }

  label,input,textarea {
    display: block;
  }

  input,textarea {
    width: 100%;
    margin-bottom: 4rem;
    border-radius: 1rem;
    border: none;
    padding: 1rem;
    @media #{$mq_tab}{
      margin-bottom: 6rem;
    }
  }

  &__label {
    text-align: left;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 2rem;
    @media #{$mq_tab}{
      font-size: 3rem;
    }
    .must {
      margin-left: 2rem;
      background: #ffff80;
      border-radius: 6rem;
      padding: .1rem 1rem;
    }
  }

  input {
    height: 5rem;
    @media #{$mq_tab}{
      height: 7rem;
    }
  }

  textarea {
    height: 30rem;
    @media #{$mq_tab}{
      height: 50rem;
    }
  }

  &__btn {
    background: #ffff80;
    font-size: 2rem;
    font-weight: bold;
    padding: 2rem 8rem;
    border-radius: 3rem;
    @media #{$mq_tab}{
      font-size: 5rem;
      padding: 0;
      width: 100%;
      height: 15rem;
      border-radius: 8rem;
    }
    &:hover {
      opacity: .8;
    }
  }
}