@charset "UTF-8";

.message {

  &__list-1column {
    margin: 0 auto;
    padding: 0 0 9rem;
    border-bottom: 1px dotted #000;
  }

  &__title {
    font-size: 3rem;
    margin-bottom: 3rem;
    @media #{$mq_sp}{
      font-size: 6rem;
      margin-bottom: 5rem;
    }
  }

  &__text {
    line-height: 3rem;
    font-weight: bold;
    color: #333;
    @media #{$mq_tab}{
      font-size: 3rem;
      line-height: 6rem;
      text-align: left;
      font-weight: 500;
    }
  }

  &__list-2column {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-top: 9rem;
    @media #{$mq_tab}{
      display: block;
    }


    .message__item {
      width: 45%;
      @media #{$mq_tab}{
        width: 100%;
        margin-bottom: 9rem;
      }
    }
  }
}