@charset "UTF-8";

.works {
  background: #4da6ff;

  &__link {
    display: block;
    background: #000;
    color: #4da6ff;
    font-weight: bold;
    padding: 3rem 4rem;
    width: 20rem;
    border-radius: 5rem;
    margin: 0 auto 5rem;
    @media #{$mq_tab}{
      width: 80%;
      padding: 5rem;
      border-radius: 7rem;
      font-size: 4rem;
      margin-bottom: 7rem;
    }
  }
}


.single { 
  min-width: 30rem; 
  margin: 0 auto 50px;
  @media #{$mq_tab}{
    max-width: 100%;
    margin: 0 auto 12rem;
  }
}

.single img { 
  width: 100%;
}